import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { User, Recipe, Database } from '../../providers/database';
/**
 * Generated class for the Profile page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html',
})
export class Profile {
  recipies: any;
  user: User;
  show: boolean = true;
  constructor(public navCtrl: NavController, public navParams: NavParams, public db: Database) {
    if (this.navParams.get('user')) {
      this.user = this.navParams.get('user');
    } else {
      this.user = db.getMe();
      this.show = false;
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Profile');
    this.recipies = <Recipe []> this.db.getFavoriteRecipies();
    /*this.recipies = [
      {
        image: 'url(assets/img/foody-mobile-12.jpg)',
        name: 'Avocado Ice Cream',
        username: 'Nam dapidu',
        liked: true
      },
      {
        image: 'url(assets/img/reference_pic1.png)',
        name: 'Ice Cream Truffles',
        username: 'Quang Anh',
        liked: false
      },
      {
        image: 'url(assets/img/reference_pic1.png)',
        name: 'Ice Cream Chocolate',
        username: 'Breyers',
        liked: false
      },
    ];*/
  }

  likeToggle(recipe: any) {
    recipe.liked = !recipe.liked;
  }

  direction(recipe) {
    this.navCtrl.push('Direction', {recipe: recipe});
  }

  chat(user) {
    this.navCtrl.push('Chat', {to: user});
  }
  
  /**
   * Map & Search
   */
  map() {
    this.navCtrl.setRoot('Map');
  }
  search() {
    this.navCtrl.setRoot('Search');
  }
}



